import { ProductStatus } from "generated/prisma";
import { CreateProductDto } from "../types/dtos/product.dto";

export type ProductForm = {
  productCode: string;
  name: string;
  description: string;
  category: string;
  quantity: number;
  unitPrice: number;
  entryDate: string;
  supplier: string;
  status: ProductStatus;
  weight: number;
  observations: string;
  serialNumber: string;
};

export const getDefaultProductForm = (): ProductForm => ({
  productCode: '',
  name: '',
  description: '',
  category: '',
  quantity: 0,
  unitPrice: 0.0,
  entryDate: new Date().toISOString().split('T')[0],
  supplier: '',
  status: ProductStatus.AVAILABLE,
  weight: 0.0,
  observations: '',
  serialNumber: ''
});

export const convertProductFormToDto = (form: ProductForm): CreateProductDto => ({
  productCode: form.productCode.trim() || null,
  name: form.name.trim(),
  description: form.description.trim() || null,
  category: form.category.trim(),
  quantity: form.quantity || null,
  unitPrice: form.unitPrice || null,
  entryDate: form.entryDate.trim() ? new Date(form.entryDate) : new Date(),
  supplier: form.supplier.trim() || null,
  status: form.status,
  weight: form.weight || null,
  observations: form.observations.trim() || null,
  serialNumber: form.serialNumber.trim() || null
});